// 列點是提示文字
.small-note {
	position: relative;
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
	margin: 0;
	list-style: none;

	&::before {
		content: attr(data-icon);
		margin-right: rem-calc(5);
	}

	.highlight {
		color: $primary-orange;
	}

	&__txt {
		font-size: rem-calc(12);
		line-height: 1.42;
		letter-spacing: normal;
		color: $bfDarkGray;

		&:not(:last-of-type) {
			margin-bottom: rem-calc(8);
		}
	}

	&.color-gray {
		color: #666;
	}

	&.info {
		&::before {
			@include font-icon-add(info-circle, false);

			opacity: 0.7;
			transform: translate(0, -2px);
		}
	}
}
