// pd-category
.page-category {
	$root: &;

	position: relative;

	&__item {
		margin-right: rem-calc(12);
		padding: rem-calc(20 0);

		&.is-active {
			#{$root}__btn {
				color: $white;
				background-color: $primary-color;
			}
		}
	}

	&__list {
		padding-left: rem-calc(20);
		display: flex;
		align-items: center;
		justify-content: flex-start;
		overflow-y: auto;
	}

	&__btn {
		padding: rem-calc(6 15);
		border-radius: rem-calc(18);
		color: $color-tip-dark;
		white-space: nowrap;
		transition: 0.36s;
	}

	&.bd-bottom {
		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			display: block;
			width: calc(100% - 40px);
			height: rem-calc(1);
			background-color: rgba($border-light, 0.3);
		}
	}
}

