// 說明 (完整說明見) src/sass/style.sass
//	* 此檔案包含：通用於所有SCSS/SASS變數檔案
//      - 變數、mixin、function
//      - 所有單元都建議須要載入之樣式
//      - 避免寫實際的樣式於該檔案內（包含檔案內 @import 之其它CSS檔案）

@charset 'utf-8';
// --------------------------------------------------------

// Vendor

// Icon Font
@import '../vendor/font/icons'; // 載入Icon Font 變數
@import '../foundation/foundation';

// Function
@import '../vendor/trig'; // 三角函數
@import '../vendor/photoshop-function'; // Photoshop 效果轉換
@import '../vendor/function'; // 常用的 Function

// --------------------------------------------------------

// Variable
// ※顏色命名同步 Zeplin
$bfDark: #333;
$bfBlueDark: #2b3843;
$bfDarkGray: #666;
$bfMediumGray: #999;
$bfGray: #ccc;
$bfLightGray: #ddd;

$bfWarning: #fa8c16;
$bfDanger: #ed5b4c;
$bfSecondaryA: #ff9500;
$bfLightSecondaryA: #ffb54c;
$bfDarkSecondaryA: #f57902;

// ===

$primary: red; // Foundation 主顏色變數 #26d07c
$primary-color-lighten: #94e4b3;
$primary-dark: $bfBlueDark; // 主要深色
$primary-dark-05: #959ba1; // 主要深色 opacity 在 白底上的顏色
$primary-orange: $bfSecondaryA;
$primary-orange-dark: $bfDarkSecondaryA;

@function primary-gradient-orange($deg: 134deg) {
	@return linear-gradient($deg, #ffb54c 22%, #ff9500 79%);
}
$primary-gradient-orange: primary-gradient-orange();

$price-color: $bfSecondaryA;

$color-tip: $bfMediumGray; // #999
$color-tip-dark: $bfDarkGray; // #666

$title-dark: $bfBlueDark; // 標題主色

$content-bg-color: #f7f9fb; // 內容區的背景

$font-bold-m: 500;
$font-bold-b: 700; // 設計稿上是 600
$font-bold: $font-bold-m;

$border-light: $bfLightGray;
$border-lightest: rgba($bfLightGray, 0.3);
$border-global: 1px solid $border-lightest;
// ===

$shadow-panel: 0 2px 20px 0 rgba($black, 0.1);
$shadow-panel-bottom: 0 -4px 10px 0 rgba($black, 0.04); // Fixed 在下方的面板，影子會上移
$shadow-blue-s: 0 2px 4px 0 rgba(221, 233, 242, 0.7);
$shadow-blue: 0 0 20px 0 rgba(221, 233, 242, 0.7);
$shadow-green: 0 0 12px 0 rgba(38, 208, 124, 0.4);
$shadow-hover: $shadow-green;

// ===

// Foundation Variable
// Gutter
$gutter-s: map-get($grid-margin-gutters, small);
$gutter-s-half: $gutter-s / 2;
$gutter-m: map-get($grid-margin-gutters, medium);
$gutter-m-half: $gutter-m / 2;

$xs: 375;
$xs-down: ($xs - 1) down;

// Button
$button-hollow-hover-lightness: -15%;
$button-opacity-disabled: 0;

// grid-container padding-x
// 同Breakpoint名稱 small => s; medium => ,\
$padding-x-global-s: rem-calc(20);
$padding-x-global-m: rem-calc(50);
$padding-x-global-l: rem-calc(100);
$padding-x-global-xxl: rem-calc(160);
$padding-x-global: $padding-x-global-s;

// ===

// Any Other Variable
// Header Height
$hd-height-s: rem-calc(44);
$hd-height-m: rem-calc(56);
$hd-height: $hd-height-s;

// Tabs
$tab-margin: 0;
$tab-background: $white;
$tab-color: $bfDarkGray;
$tab-background-active: transparent;
$tab-active-color: $title-dark;
$tab-item-font-size: rem-calc(15);
$tab-item-background-hover: $white;
$tab-item-padding: rem-calc(15 10);
$tab-content-background: $white;
$tab-content-border: transparent;
$tab-content-color: $body-font-color;
$tab-content-padding: 0;
// Loading 中尚未讀取的背景顏色
$loading-bg: #eee;

// --------------------------------------------------------
// <select>
// Select Arrow
@function select-arrow-builder($color: $body-font-color) {
	$color-normal: str-replace('' + $color, '#', '');
	// $build-svg: 'data:image/svg+xml,%3Csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 115.107 53.616"%3E%3Cpolyline fill="none" stroke="%23#{$color-normal}" stroke-width="7" stroke-linecap="round" stroke-linejoin="round" points="3.5 3.501 57.553 50.116 111.607 3.501"/%3E%3C/svg%3E';
	$build-svg: 'data:image/svg+xml,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%2014.02%208.42%22%3E%3Cpath%20d%3D%22M14%2C1.42l-7%2C7-7-7L.67%2C0%2C7%2C6.33%2C13.34%2C0Z%22%20fill%3D%22%23#{$color-normal}%22%20fill-rule%3D%22evenodd%22%2F%3E%3C%2Fsvg%3E';
	@return $build-svg;
}
$select-arrow: select-arrow-builder(#999);
$select-arrow-white: select-arrow-builder($white);

// Select Style
@mixin select-style($use-white: false) {
	padding-right: rem-calc(40);
	text-overflow: ellipsis;
	cursor: pointer;

	// Arrow
	background: no-repeat right 10px center / 16px 10px; // IE 一定要 width, height 都寫，不然大小會不對

	@if $use-white {
		background-image: url('#{$select-arrow-white}');
	} @else {
		background-image: url('#{$select-arrow}');
	}

	@include breakpoint(medium) {
		padding-right: rem-calc(55);
	}
}

// --------------------------------------------------------

// Function
@function -zf-font-size-get($bp, $font-size) {
	@return map-get(map-get(map-get($header-styles, $bp), $font-size), 'font-size');
}

@mixin font-size-build($font-size) {
	font-size: rem-calc(-zf-font-size-get('small', $font-size));

	@include breakpoint(medium) {
		font-size: rem-calc(-zf-font-size-get('medium', $font-size));
	}
}

// --------------------------------------------------------

// Mixin

// Flex
@mixin fc($justify, $align) {
	display: flex;

	@if $justify {
		justify-content: $justify;
	}

	@if $align {
		align-items: $align;
	}
}

// 遵循BEM的規則，於class增加tag（僅限於單層方式，多層不適用）
// 可使用（單層）： .component__link => a.component__link
// 不適用（多層）： .component .component__link => .component a.component__link
@mixin bem-tag($tag: 'a') {
	$current: &;

	@at-root #{$tag}#{$current} {
		@content;
	}
}

// 使用 ::before 的 padding-top 撐高度
@mixin before-img-size($width: 1, $height: 1, $build-other: true) {
	@if $build-other {
		content: '';
		display: block;
	}

	padding-top: percentage($height / $width);
}

// ---------------------------------------- [START] Media Query 相關
// Hover Media Query (使用 Foundation Mixin)
$project-has-ie: true; // 專案是否有含 IE => 有 IE 就不能使用 @media hover

@mixin breakpoint-hover($hover-enable: true) {
	// 有 IE 專案
	@if $project-has-ie {
		@if $hover-enable {
			@include breakpoint(xlarge) {
				@content;
			}
		} @else {
			@include breakpoint(xlarge down) {
				@content;
			}
		}
	}

	// 無 IE 專案
	@else {
		@if $hover-enable {
			@media (hover: hover) {
				@content;
			}
		} @else {
			@media (hover: none) {
				@content;
			}
		}
	}
}

// Breakpoint Between (使用 Foundation Mixin)
@mixin breakpoint-between($start, $end) {
	@media screen and #{breakpoint($start)} and #{breakpoint($end down)} {
		@content;
	}
}

// 2x, 3x
@mixin breakpoint-2x {
	@media screen and (-webkit-min-device-pixel-ratio: 2), screen and (min--moz-device-pixel-ratio: 2) {
		@content;
	}
}

@mixin breakpoint-3x {
	@media screen and (-webkit-min-device-pixel-ratio: 3), screen and (min--moz-device-pixel-ratio: 3) {
		@content;
	}
}

// ---------------------------------------- [END] Media Query 相關

// ---------------------------------------- [START] Browser Hack
// Browser
@mixin ie() {
	@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
		@content;
	}
}
// ---------------------------------------- [END] Browser Hack

// ---------------------------------------- [START] No Selection | 無法反選
// No Selection | 無法反選
// https://stackoverflow.com/a/4407335/11240898
@mixin selection-none {
	-webkit-touch-callout: none; // iOS Safari
	-webkit-user-select: none; // Safari
	-khtml-user-select: none; // Konqueror HTML
	-moz-user-select: none; // Firefox
	-ms-user-select: none; // Internet Explorer/Edge
	user-select: none; // Non-prefixed version, currently, supported by Chrome and Opera
}
// ---------------------------------------- [END] No Selection | 無法反選

// ---------------------------------------- [START] Custom Scroll Bar
$scrollbar-thumb-color: $primary-color; // 操作顏色
$scrollbar-track-color: #eee; // 滾軸背景
$scrollbar-width: 6px; // 寬度(瀏覽器預設為15px)
$scrollbar-border-radius: 6px; // 圓角，Firefox不支援

@mixin custom-scroll-bar(
	$thumb-color: $scrollbar-thumb-color,
	$track-color: $scrollbar-track-color,
	$track-width: $scrollbar-width,
	$track-border-radius: $scrollbar-border-radius
) {
	// For Chrome, Opera, Safari, Android, iOS
	&::-webkit-scrollbar {
		width: $track-width;
		height: $track-width;
	}

	&::-webkit-scrollbar-track {
		background: $track-color;
	}

	&::-webkit-scrollbar-thumb {
		background: $thumb-color;
		border-radius: $track-border-radius;

		&:hover {
			background: darken($thumb-color, 10%);
		}
	}

	@if & {
		// For Firefox (版本必須64+)
		// 如果是整頁<body>要使用，必須將Class增加於<html>標籤上
		// Ref: https://stackoverflow.com/a/6165489/11240898
		// 顏色：操作顏色(Thumb) 滾軸背景(Track)
		scrollbar-color: $thumb-color $track-color;
		// 寬度只有 auto | thin | none 選項
		// auto為15px，thin為6px，none為不顯示
		scrollbar-width: thin;
	}
}

// ---------------------------------------- [END] Custom Scroll Bar

// ---------------------------------------- [START] Clamp
// @include clamp-line-height(最多幾行, $line-height);
// 因為有IE，所以不能使用 variable CSS 改
@mixin clamp-line-height($line, $line-height) {
	line-height: $line-height;
	max-height: calc(1em * #{$line-height} * #{$line});
}
// ---------------------------------------- [END] Clamp

// ---------------------------------------- [START] From
// 隱藏 Checkbox, Radio 的方式（input(type="checkbox/radio") 的物件）
// 外層要記得增加 position: relative
@mixin checkbox-hide {
	position: absolute;
	background: 0 0;
	border-top: 0 none;
	border-right: 0 none;
	border-left: 0 none;
	opacity: 0;
	box-shadow: none;
	cursor: pointer;
}
// ---------------------------------------- [END] From

// ---------------------------------------- [START] Animation Set
@mixin set-animate($animateEl) {
	@each $class in $animateEl {
		$i: index($animateEl, $class);
		$i: $i;

		#{$class} {
			opacity: 0;
			transform: translateY(50px);
			transition-duration: 1s;
			transition-property: opacity, transform;
			transition-delay: #{$i * 0.3s};
		}
	}
}

@mixin set-animated($animateEl) {
	@each $class in $animateEl {
		#{$class} {
			opacity: 1;
			transform: none;
		}
	}
}

@mixin set-animate-flow($animateEl) {
	&.js-ani {
		@include set-animate($animateEl);

		&.is-animated {
			@include set-animated($animateEl);
		}
	}
}

// ---------------------------------------- [END] Animation Set

// ---------------------------------------- [START] Arrow (Function)
@function ani-arrow($dir: right, $tsd: 2s) {
	@return arrow-opacity $tsd infinite, arrow-to-#{$dir} $tsd infinite;
}
// ---------------------------------------- [END] Arrow (Function)

// ---------------------------------------- [START][Project] Border Gradient
@mixin border-gradient($bg: $white) {
	// border-image-source 不能搭配 border-radius，border-radius 會無法作用
	// ※Zeplin 產生的樣式無法使用
	// border-style: solid;
	// border-width: 1px;
	// border-image-source: linear-gradient(to bottom, #e1e7ee, #dae2e7);
	// border-image-slice: 1;

	border: 1px solid transparent;

	// 為了要讓 border 漸層 + border-radius 並存的方式
	// Ref: https://stackoverflow.com/a/51496341
	background: linear-gradient($bg, $bg) padding-box,
		/*this is your grey background*/ linear-gradient(to bottom, #edf0f3, #dee3e6) border-box;
}
// ---------------------------------------- [END][Project] Border Gradient

// ---------------------------------------- [START][Project] 產品內頁 Pd-sec style
// SASS Placeholder Selector + BEM 會出現生成問題
// 相關討論與解法：https://github.com/sass/sass/issues/1424#issuecomment-55279760
%pd-sec {
	padding: rem-calc(18) 0;
	background: var(--bg-color);

	@include breakpoint(medium) {
		padding: rem-calc(30) 0;
	}

	&__cont {
		position: relative;
		padding: 1rem;
		background: #fff;
		box-shadow: $shadow-blue;
		border-radius: rem-calc(6); // mb pc 統一為 6px
	}
}

@mixin pd-sec() {
	@extend %pd-sec;
	&__cont {
		@extend %pd-sec__cont;
	}
}
// ---------------------------------------- [END][Project] 產品內頁 Pd-sec style
