// Main Style
@import 'layout/variable_mixin_function.scss';
// --------------------------------------------------------

/* Component */
// List
@import './components/pd-category';

// Cont
@import './components/pd-banner';
@import './components/pd-history';

@import './components/list-small-note';

.sec-pd {
	@include pd-sec;
}

// --------------------------------------------------------

:root {
	--bg-color: #{$content-bg-color};
}

// 產品內容頁 => 標題一開始不顯示
.hd-pd-cont {
	&:not(.is-collapse) {
		.hd {
			&__c {
				opacity: 0;
			}
		}
	}

	.hd {
		&__c {
			transition: opacity 0.3s;
		}
	}
}

.pd-nav {
	background: #f6f9fb;
}

.tabs-panel {
	.grid-container {
		@include breakpoint(small down) {
			padding-left: 0;
			padding-right: 0;
		}
	}
}

.tabs-content {
	background-color: $content-bg-color;

	.btn-load-more {
		margin-top: rem-calc(20);
	}
}

// --------------------------------------------------------

// 刊登金額設定
.pd-publish-price {
	padding: rem-calc(35 0);

	// price-group
	.price-group {
		margin-bottom: rem-calc(30);

		.input {
			margin-bottom: rem-calc(8);
			border-radius: rem-calc(12);
			border: rem-calc(1) solid $bfMediumGray;
			font-size: rem-calc(20);
			font-weight: 500;
			height: rem-calc(60);
			line-height: 1;
			letter-spacing: normal;
			text-align: right;
			color: $bfBlueDark;
		}
	}

	// Button
	&__button {
		margin-top: rem-calc(40);
		margin-bottom: 0;
		width: 100%;
	}
}



// --------------------------------------------------------

// List
.pd-list-publish {
	padding: 0 0 rem-calc(18);

	.pd-card__title {
		font-weight: $font-bold-b;
	}

	.pd-card--hori {
		margin-left: 0;
	}

	.grid-container {
		@include breakpoint(small down) {
			padding: 0;
		}
	}
}

.checkbox + label {
	@include breakpoint(small down) {
		font-size: rem-calc(14);
	}
}

//
.accordion-title {
	.text-arrow {
		.be-icon {
			margin-left: rem-calc(3);
		}
	}
	&[aria-expanded='true'] {
		.sec-title-box__r {
			.be-icon {
				transform: rotate(180deg);
				display: inline-block;
			}
		}
	}
}
