// 交易履歷
.pd-history {
	$activeColor: #11b8ea;

	@include pd-sec;

	// 總結
	&__report {
		display: flex;

		&__item {
			flex-grow: 1;
			flex-basis: auto;
			font-size: rem-calc(13);
			text-align: center;
		}

		&__title {
			margin-bottom: 0;
			font-size: inherit;
			font-weight: normal;
		}

		&__num {
			font-size: em-calc(20 / 13);
			font-weight: 500;
		}
	}

	// 類型
	&__type {
		&__list {
			display: flex;
			justify-content: space-around;
		}

		&__link {
			padding: 3px 8px;
			font-size: 13px;
			font-weight: 500;
			line-height: 1.38;
			color: #666;
			border: solid 1px #ddd;
			border-radius: 4px;
			box-sizing: border-box;

			&.is-active {
				pointer-events: none;
				color: $white;
				background: $activeColor;
				border-color: $activeColor;
			}
		}
	}

	&__chart-box {
		canvas {
			width: 100%;
			height: 100%;
		}

		// Tooltip 樣式
		// 為了要蓋過 JS 產生的 inline Style ，所以都要用 !important
		.echarts-tooltip {
			line-height: 1.2 !important;
			text-align: center;
			color: var(--active-color) !important;
			background-color: transparent !important;
			border-color: transparent !important;
			box-shadow: none !important;
		}
	}

	&__chart {
		margin-top: rem-calc(-20);
		width: 100%;
		height: (220px + 50);
	}

	&__accordion {
		background-color: transparent;

		.accordion {
			&-item {
				&.is-active {
					.accordion-title {
						margin-bottom: 0.875rem;
					}
				}
			}

			&-title {
				padding: 0;
				border: 0 !important;
				overflow: hidden;
				transition: margin 0.3s;

				&:hover,
				&:focus {
					background-color: transparent;
				}

				&::before {
					content: none;
				}
			}

			&-content {
				padding: 0;
				border: 0 !important;
			}

			.sec-title-box {
				margin-bottom: 0;
			}
		}
	}

	@include breakpoint(large down) {
		&__top {
			margin-left: auto;
			margin-right: auto;
			max-width: rem-calc(320);
		}
	}

	// PC style
	@include breakpoint(xlarge) {
		&__top {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}

		&__report {
			margin-bottom: 0;
			&__item {
				white-space: nowrap;

				&:not(:first-child) {
					margin-left: rem-calc(44);
				}
			}
		}

		&__type {
			&__list {
				margin-bottom: 0;
			}

			&__item {
				&:not(:first-child) {
					margin-left: rem-calc(12);
				}
			}
		}
	}
}
